import React, { Component } from "react";
import { Query } from 'react-apollo';
import gql from 'graphql-tag';
import { withRouter } from 'react-router-dom';
import Loading from './loading';
import ArticleList from './articlelist';
import LabelList from './labelList';


const labelsQuery = gql`
{
  repository(owner: "isNeilLin", name: "note") {
    labels(first: 100) {
      nodes {
        name
        issues(first: 100) {
          edges {
            node {
              id
              title
              labels(first: 20) {
                edges {
                  node {
                    id
                    name
                    color
                  }
                }
              }
              publishedAt
              bodyText
              bodyHTML
            }
          }
        }
      }
    }
  }
}
`


class Labels extends Component {
    emptyParam = ()=>{
        return (<div className="labelList">
            <Query query={labelsQuery}>
            {({loading, error, data})=>{
                if(loading) return <Loading/>
                if(error) return (<div>{error.graphQLErrors.map(gqlError=>(
                    <p className="errorMessage">{gqlError.message}</p>
                ))}</div>);
                return <LabelList labels={data.repository.labels.nodes}/>
            }}
            </Query>
        </div>)
    }
    
    hasParam = ()=>{
        let label = this.props.location.state[0];
        return (<div className="LabelContent">
            <p className="labelListTitle">{label.name}</p>
            <ArticleList issues={label.issues.edges}/>
        </div>)
    }

    render(){
        if (this.props.location.state && this.props.location.state.length){
            return this.hasParam()
        }else {
            return this.emptyParam()
        }
    }
}

export default withRouter(Labels);


