import React, { Component } from "react";

export default class Label extends Component {
    render(){
        let labelStyle = {
            display: 'inline-block',
            background: `#${this.props.color}`,
            padding: '0px 4px',
            borderRadius: '4px',
            color: 'white',
            marginLeft: '6px',
            marginRight: '6px'
        };
        return (
            <span className="labelWithStyle" style={labelStyle}>
                {this.props.name}
            </span>
        )
    }
}


