import React, { Component } from "react";
import { Query } from 'react-apollo';
import gql from 'graphql-tag';
import Loading from './loading';

const Repositories = gql`
query Repositories{
  user(login: "isNeilLin") {
    repositories(first:6,orderBy:{field:STARGAZERS, direction:DESC}){
      nodes {
        id,
        name,
        url,
        shortDescriptionHTML,
        forkCount,
        primaryLanguage {
          name,
          color
        },
        stargazers(first:100) {
          edges {
            node {
              id
            }
          }
        },
        watchers(first:100) {
          edges {
            node {
              id
            }
          }
        }
      }
    }
  }
}`

export default class About extends Component {
    render(){
        return (
            <div className="AboutLayer">
                <div>
                    <img className="avatar" src="data:image/jpeg;base64,"/>
                </div>
                <h2>Neil Lin</h2>
                <div>
                    <Query query={Repositories}>
                    {({loading, error, data})=>{
                        if(loading) return <Loading/>
                        if(error) return (<div>{error.graphQLErrors.map(gqlError=>(
                            <p className="errorMessage">{gqlError.message}</p>
                        ))}</div>);
                        return <div><Repository data={data.user.repositories.nodes}></Repository></div>
                    }}
                    </Query>
                </div>
                <p className="links">
                    <a href="https://github.com/isNeilLin">Github主页</a>
                    <a href="https://juejin.im/user/59cbc2c06fb9a00a5143acf2">掘金主页</a>
                    <a href="mailto:neil3397@gmail.com">neil3397@gmail.com</a>
                </p>
            </div>
        )
    }
}

class Repository extends Component {
    buildLang(lang){
        if(lang){
            const styleObj = {
                display: 'inline-block',
                marginRight: '8px',
                width: '12px',
                height: '12px',
                borderRadius: '6px',
                backgroundColor: lang.color
            }
            return (
                <span>
                    <span style={styleObj}></span>
                    {lang.name}
                </span>
            )
        }
        return <span></span>;
    }
    buildInfo(rep){
        return (<span className="countInfo">
            <svg aria-label="stars" className="octicon octicon-star" viewBox="0 0 14 16" version="1.1" width="14" height="16" role="img"><path fillRule="evenodd" d="M14 6l-4.9-.64L7 1 4.9 5.36 0 6l3.6 3.26L2.67 14 7 11.67 11.33 14l-.93-4.74L14 6z"></path></svg>
            <span className="count">{rep.stargazers.edges.length}</span>
            <svg aria-label="fork" className="octicon octicon-repo-forked" viewBox="0 0 10 16" version="1.1" width="10" height="16" role="img"><path fillRule="evenodd" d="M8 1a1.993 1.993 0 0 0-1 3.72V6L5 8 3 6V4.72A1.993 1.993 0 0 0 2 1a1.993 1.993 0 0 0-1 3.72V6.5l3 3v1.78A1.993 1.993 0 0 0 5 15a1.993 1.993 0 0 0 1-3.72V9.5l3-3V4.72A1.993 1.993 0 0 0 8 1zM2 4.2C1.34 4.2.8 3.65.8 3c0-.65.55-1.2 1.2-1.2.65 0 1.2.55 1.2 1.2 0 .65-.55 1.2-1.2 1.2zm3 10c-.66 0-1.2-.55-1.2-1.2 0-.65.55-1.2 1.2-1.2.65 0 1.2.55 1.2 1.2 0 .65-.55 1.2-1.2 1.2zm3-10c-.66 0-1.2-.55-1.2-1.2 0-.65.55-1.2 1.2-1.2.65 0 1.2.55 1.2 1.2 0 .65-.55 1.2-1.2 1.2z"></path></svg>
            <span className="count">{rep.forkCount}</span>
            <svg className="octicon octicon-eye v-align-text-bottom" viewBox="0 0 16 16" version="1.1" width="16" height="16" aria-hidden="true"><path fillRule="evenodd" d="M8.06 2C3 2 0 8 0 8s3 6 8.06 6C13 14 16 8 16 8s-3-6-7.94-6zM8 12c-2.2 0-4-1.78-4-4 0-2.2 1.8-4 4-4 2.22 0 4 1.8 4 4 0 2.22-1.78 4-4 4zm2-4c0 1.11-.89 2-2 2-1.11 0-2-.89-2-2 0-1.11.89-2 2-2 1.11 0 2 .89 2 2z"></path></svg>
            <span className="count">{rep.watchers.edges.length}</span>
        </span>)
    }

    buildRep(){
        return this.props.data.map(rep=>{
            console.log(rep)
            return (<div className="rep" key={rep.id}>
                <p className="repName"><a href={rep.url} target="_blank">{rep.name}</a></p>
                <p className="repDesc">{rep.shortDescriptionHTML||'No Description'}</p>
                <p className="repInfo">{this.buildLang(rep.primaryLanguage)} {this.buildInfo(rep)}</p>
            </div>)
        })
    }

    render(){
        return <div className="reps">{this.buildRep()}</div>
    }
}


