import React, { Component } from "react";
import { ApolloClient } from "apollo-client";
import { createHttpLink } from "apollo-link-http";
import { setContext } from "apollo-link-context";
import { ApolloProvider } from "react-apollo";
import { InMemoryCache } from "apollo-cache-inmemory";
import { BrowserRouter as Router, Route, NavLink, Switch } from "react-router-dom";
import Home from './components/home';
import Labels from './components/labels';
import Archive from './components/archive';
import About from './components/about';
import IssueDetail from './components/issueDetail';
import NoMatch from './components/404';
import "./App.css";

const httpLink = createHttpLink({
  uri: "https://api.github.com/graphql"
});
const authLink = setContext((_, { headers }) => {
  
  let token = ["aace66c25","453951027e065","9e92b6972f4637cbe5"];
  return {
    headers: {
      ...headers,
      authorization: `Bearer ${token.join('')}`
    }
  };
});

const client = new ApolloClient({
  link: authLink.concat(httpLink),
  cache: new InMemoryCache()
});

const activeStyle = {
  "textDecoration": "underline",
  "color": "#87ceeb"
}

class App extends Component {
  render() {
    return (
      <ApolloProvider client={client}>
       <Router>
          <div className="App">
            <header className="App-header">
              <ul className="header">
                <li><NavLink  exact to="/" activeStyle={activeStyle}>首页</NavLink></li>
                <li><NavLink to="/labels" activeStyle={activeStyle}>标签</NavLink></li>
                <li><NavLink to="/archive" activeStyle={activeStyle}>归档</NavLink></li>
                <li><NavLink to="/about" activeStyle={activeStyle}>关于我</NavLink></li>
              </ul>
              <div>
                <img src="data:image/jpeg;base64,"/>
                Neil Lin
              </div>
            </header>
            <Switch>
            <Route exact path="/" component={Home} />
            <Route path="/labels" component={Labels} />
            <Route path="/labels/:label" component={Labels} />
            <Route path="/archive" component={Archive} />
            <Route path="/archive/:month" component={Archive} />
            <Route path="/about" component={About} />
            <Route path="/issue" component={IssueDetail} />
            <Route exact component={NoMatch} />
            </Switch>
          </div>
        </Router>
      </ApolloProvider>
    );
  }
}

export default App;



