import React, { Component } from "react";
import { withRouter } from 'react-router-dom';

class ArchiveList extends Component {
    skipArchive = (month, issues)=>{
        this.props.history.push(`/archive/${month}`,issues)
    }
    getArchives = ()=>{
        let data = {};
        this.props.issues.map(issue=>{
            let month = issue.node.publishedAt.slice(0,7);
            if(data[month]){
                data[month].push(issue)
            }else{
                data[month] = [issue]
            }
        })
        return Object.keys(data).map(m=>{
            let issues = data[m];
            return <span className="labelName" key={m} onClick={this.skipArchive.bind(this, m, issues)}>{m} ({issues.length})</span>
        })
    }
    render(){
        return (
            <div className="labelList">
                {this.getArchives()}
            </div>
        )
    }
}

export default withRouter(ArchiveList);


