import React, { Component } from "react";
import { Query } from 'react-apollo';
import gql from 'graphql-tag';
import Loading from './loading';
import ArticleList from './articlelist';

const recentlyIssues = gql`
query Res($endCursor: String){
    repository(owner: "isNeilLin", name: "note") {
        issues(first: 5,after: $endCursor,orderBy:{direction:DESC,field:CREATED_AT}) {
            totalCount
            pageInfo {
                startCursor
                endCursor
                hasNextPage
                hasPreviousPage
            }
            edges {
                node {
                    id
                    title
                    labels(first: 20) {
                        edges {
                            node {
                                id
                                name
                                color
                            }
                        }
                    }
                    publishedAt
                    bodyText
                    bodyHTML
                }
            }
        }
    }
}`
const prevIssues = gql`
query Res($startCursor: String){
    repository(owner: "isNeilLin", name: "note") {
        issues(last: 5,before: $startCursor,orderBy:{direction:DESC,field:CREATED_AT}) {
            totalCount
            pageInfo {
                startCursor
                endCursor
                hasNextPage
                hasPreviousPage
            }
            edges {
                node {
                    id
                    title
                    labels(first: 20) {
                        edges {
                            node {
                                id
                                name
                                color
                            }
                        }
                    }
                    publishedAt
                    bodyText
                    bodyHTML
                }
            }
        }
    }
}`

export default class Home extends Component {
    render(){
        return (
        <div className="HomeContent">
            <Query query={recentlyIssues} variables={{first: 5}}>
                {({loading, error, data, fetchMore})=>{
                    if(loading) return <Loading/>
                    if(error) return (<div>{error.graphQLErrors.map(gqlError=>(
                        <p className="errorMessage">{gqlError.message}</p>
                    ))}</div>);
                    return <HomeContent issues={data.repository.issues} fetchMore={fetchMore} loading={loading}/>
                }}
            </Query>
        </div>
        )
    }
}

class HomeContent extends Component {
    constructor(props){
        super(props);
        this.state = {
            isLoading: false,
            hasNext: this.props.issues.pageInfo.hasNextPage,
            hasPrev: this.props.issues.pageInfo.hasPreviousPage,
            startCursor: this.props.issues.pageInfo.startCursor,
            endCursor: this.props.issues.pageInfo.endCursor
        }
    }
    
    prevPage = ()=>{
        if(this.state.hasPrev){
            this.setState({isLoading: true});
            this.props.fetchMore({
                query: prevIssues,
                variables: {
                    startCursor: this.state.startCursor
                },
                updateQuery: (previousResult, { fetchMoreResult })=>{
                    let newState = fetchMoreResult.repository.issues;
                    this.setState({
                        hasNext: newState.pageInfo.hasNextPage,
                        hasPrev: newState.pageInfo.hasPreviousPage,
                        startCursor: newState.pageInfo.startCursor,
                        endCursor: newState.pageInfo.endCursor,
                        isLoading: false
                    })
                    return fetchMoreResult;
                }
            })
        }
    }

    nextPage = ()=>{
        if(this.state.hasNext){
            this.setState({isLoading: true});
            this.props.fetchMore({
                query: recentlyIssues,
                variables: {
                    endCursor: this.state.endCursor
                },
                updateQuery: (previousResult, { fetchMoreResult })=>{
                    let newState = fetchMoreResult.repository.issues;
                    this.setState({
                        hasNext: newState.pageInfo.hasNextPage,
                        hasPrev: newState.pageInfo.hasPreviousPage,
                        startCursor: newState.pageInfo.startCursor,
                        endCursor: newState.pageInfo.endCursor,
                        isLoading: false
                    })
                    return fetchMoreResult;
                }
            })
        }
    }

    render(){
        if(this.props.loading||this.state.isLoading) return <Loading />
        return (
        <div className="articleList">
            <ArticleList issues={this.props.issues.edges}/>
            <p className="skipPage">
                <a className={this.state.hasPrev ? '' : 'disable'} onClick={this.prevPage}>上一页</a>
                <a className={this.state.hasNext ? '' : 'disable'} onClick={this.nextPage}>下一页</a>
            </p>
        </div>
        )
    }
}


