import React, { Component } from "react";
import { Query } from 'react-apollo';
import gql from 'graphql-tag';
import { withRouter } from 'react-router-dom';
import Loading from './loading';
import ArticleList from './articlelist';
import ArchiveList from './archiveList';


const archiveQuery = gql`
{
  repository(owner: "isNeilLin", name: "note") {
    issues(first: 100) {
        edges {
            node {
                id
                title
                labels(first: 20) {
                    edges {
                        node {
                        id
                        name
                        color
                        }
                    }
                }
                publishedAt
                bodyText
                bodyHTML
            }
        }
    }
  }
}
`


class Archive extends Component {
    emptyParam = ()=>{
        return (<div className="archiveList">
            <Query query={archiveQuery}>
            {({loading, error, data})=>{
                if(loading) return <Loading/>
                if(error) return (<div>{error.graphQLErrors.map(gqlError=>(
                    <p className="errorMessage">{gqlError.message}</p>
                ))}</div>);
                return <ArchiveList issues={data.repository.issues.edges}/>
            }}
            </Query>
        </div>)
    }
    
    hasParam = ()=>{
        let issues = this.props.location.state;
        let title = this.props.location.pathname.split('/')[2];
        return (<div className="LabelContent">
            <p className="labelListTitle">{title}</p>
            <ArticleList issues={issues}/>
        </div>)
    }

    render(){
        if (this.props.location.state && this.props.location.state.length){
            return this.hasParam()
        }else {
            return this.emptyParam()
        }
    }
}

export default withRouter(Archive);


