import React, { Component } from "react";
import { withRouter } from 'react-router-dom';
import Label from './label';

class IssueDetail extends Component {
    constructor(props){
        super(props);
        this.state = {
            issue: this.props.location.state[0]
        }
    }

    __setHTML = ()=>{
        return {
            __html: this.state.issue.bodyHTML
        }
    }

    back = ()=>{
        try{
            this.props.history.goBack();
        }catch(e){
            this.props.history.go('/');
        }
    }

    render(){
        return (
            <div className="issueDetail">
                <div className="markdown-head">
                    <span className="backToHome" onClick={this.back}>返回</span>
                        <span>
                        {this.state.issue.labels.edges.map(({node})=>{
                            return <Label key={node.id} color={node.color} name={node.name}/>
                        })}
                        <span style={{marginLeft: "16px"}}>发布于： 
                            <span className="date">
                                {this.state.issue.publishedAt.replace(/T|Z/g,' ')}
                            </span>
                        </span>
                    </span>
                </div> 
                <div className="markdown-body" dangerouslySetInnerHTML={this.__setHTML()}></div>
            </div>
        )
    }
}

export default withRouter(IssueDetail);


